.App {
  text-align: center;
}


.color-picr{
  margin-right: 10px ;
  align-items: center;
  vertical-align: middle  ;
}
label[for="color-picker"]{
  margin-right: 5px;
}
input[type="color"].color-picker{
  padding: 0;
	border: none;
  border-radius: 50%;
	height: 10px;
	width: 30px;
	vertical-align: middle;
}
.btn{   
  justify-self: center;
  margin-left: 40%;
  
}
.mb-3{
  width: 50%;
  margin: auto;
}
.body{
  margin: 0%;
  padding: 0%;
}
.head1{
  background-color: #282c34;
  margin: 0%;
  padding: 0%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
